@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.input-group {
    position: relative;
  }
  
  .mb-b {
    margin-bottom: 30px;
  }
  
  .mb-s {
    margin-bottom: 15px;
  }
  
  .input-group__input {
    border: 1px solid;
    border-color: #ccc;
    -webkit-appearance: none;
    -ms-progress-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: transparent;
    padding: 12px;
    border-radius: 3px;
    width: 100%;
    max-width: 100%;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input-group__label {
    position: relative;
    font-size: 14px;
    padding-top: 20px;
  }
  
  .input-group__placeholder {
    position: absolute;
    left: 12px;
    padding: 5px;
    top: calc(50% + 10px);
    transform: translateY(-50%);
    color: #aaa;
    transition: all 0.3s ease;
  }
  .input-group__input:valid + .input-group__placeholder,
  .input-group__input:focus + .input-group__placeholder {
    top: 20%;
    font-size: 12px;
    color: blueviolet;
    background-color: white;
  }
  
  .input-group__input:focus {
    border: 1px solid;
    border-color: blueviolet;
  }
  
  /* Styles of input-radio */
  
  .input-group__input-radio {
    -webkit-appearance: none;
    -ms-progress-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: absolute;
  }
  
  .input-group__label-radio {
    position: relative;
    cursor: pointer;
  }
  .input-group__placeholder-radio::before {
    content: "";
    position: absolute;
    display: block;
    width: 15px;
    height: 15px;
    border: 2px solid blueviolet;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
  }
  .input-group__placeholder-radio::after {
    content: "";
    position: absolute;
    display: block;
    width: 9px;
    height: 9px;
    background-color: blueviolet;
    left: 3px;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    visibility: hidden;
    opacity: 0;
  }
  
  .input-group__input-radio:checked + .input-group__placeholder-radio::after {
    visibility: visible;
    opacity: 1;
  }
  
  .input-group__placeholder-radio {
    padding-left: 20px;
  }
  
  .error-border {
    border-color: red;
  }
  
  .error {
    position: absolute;
    left: 0;
    bottom: -20px;
    color: red;
  }
  
.select {
  position: relative;
  margin-bottom: 15px;
}

.select__title {
  border: 1px solid;
  border-color: #ccc;
  background: transparent;
  padding: 12px;
  border-radius: 3px;
  width: 100%;
  max-width: 100%;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

.select__title:hover {
  border-color: blueviolet;
}

/* drop__menu */

.dropmenu {
  width: 100%;
  max-width: 100%;
  position: absolute;
  border: 1px solid;
  border-color: #ccc;
  border-radius: 3px;
  z-index: 1000;
  margin-top: 5px;
  cursor: pointer;
}

.dropmenu__option {
  padding: 12px;
  width: 100%;
  max-width: 100%;
  background: white;
  z-index: 2000;
  border-bottom: 1px solid #ccc;
}
.dropmenu__option:hover {
  background-color: #efd9ff;
}
.dropmenu__option:last-child {
  border: none;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.select-form {
    position: relative;
    width: 100%;
    max-width: 100%;
}

/* selectedItems styles*/
.selected-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid;
    border-color: #ccc;
    background: transparent;
    padding: 12px;
    border-radius: 3px;
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    transition: border-color .3s ease;
}

.selected-items:hover {
    border-color: blueviolet;
}

.selected-items__body {
    flex: 1 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.selected-items__dashboard {
    flex: 0 1 35px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}


/* => Item */
.selected-item {
    border: 1px solid;
    border-color: #ccc;
    padding: 5px;   
    margin-left: 10px;
}

.selected-item:first-child {
    margin: 0;
}

.selected-item__body {
display: flex;
justify-content: space-between;
align-items: center;
}

.selected-item__title {
border-right: 1px solid #ccc;
padding-right: 2px;
}


.rm {
    position: relative;
    display: block;
    width: 18px;
    height: 18px;
    background-color: transparent;
    margin-left: 2px;
    border-radius: 3px;
}

.rm:hover {
    background-color: #ccc;
}

.rm::before {
    position: absolute;
    top: 50%;
    left: 22%;
    content: '';
    width: 10px;
    height: 2px;
    background-color: black;
    transform: rotate(45deg);
}
.rm::after {
    position: absolute;
    top: 50%;
    right: 22%;
    content: '';
    width: 10px;
    height: 2px;
    background-color: black;
    transform: rotate(-45deg);
}
.op {
    position: relative;
    display: block;
    width: 10px;
    height: 10px;
    background-color: transparent;
    margin-left: 2px;
    border-radius: 3px;
}

.op::before {
    position: absolute;
    top: 50%;
    right: 3px;
    content: '';
    width: 10px;
    height: 2px;
    background-color: black;
    transform: rotate(45deg);
}
.op::after {
    position: absolute;
    left: 3px;
    top: 50%;
    content: '';
    width: 10px;
    height: 2px;
    background-color: black;
    transform: rotate(-45deg);
}
/* index */

