.steps__picture {
  display: flex;
  max-width: 400px;

  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

.steps__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 30px;
}

.steps__title div {
  width: 100%;
  max-width: 50px;
  font-size: 12px;
  text-align: center;
  white-space: pre-wrap;
  font-weight: bold;
}

.element {
  position: relative;
  width: 50px;
  height: 50px;
  background-color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  z-index: 3;
}

.element__title {
  z-index: 2;
  color: white;
}

.element::before {
  content: "";
  position: absolute;
  display: block;
  width: 200%;
  height: 2px;
  background-color: grey;
  left: 50%;
  z-index: 1;
}

.element.active {
  background-color: blueviolet;
  border: 2px solid darkmagenta;
}

.element.active::before {
  background-color: blueviolet;
}

.element.completed {
  background-color: blueviolet;
}
.element.completed::before {
  background-color: blueviolet;
}

@media (max-width: 300px) {
  .steps__title {
    display: none;
  }

  .steps__picture {
    margin-bottom: 10px;
  }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.App {
  max-width: 400px;
  margin: 5% auto;
  background-color: white;
  box-shadow: 0px 0px 17px 3px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  padding: 50px 25px 75px 25px;
}

h1 {
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
}

.navigate {
  display: flex;
  justify-content: space-between;
  padding: 20px 0px;
}

.navigate button {
  width: 100px;
  padding: 10px 0px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}

button:active {
  box-shadow: inset 1px 1px 11px 2px #000000;
}

.navigate__prev {
  background-color: #ccc;
  font-size: 16px;
}

.navigate__next {
  background-color: blueviolet;
  color: white;
  font-weight: 500;
  font-size: 16px;
}

